/* tESING SPOT  */


main {
  margin: 0 auto;
  max-width: 90%;
  padding: 50px 30px;
}
:root {
  --primary: #FFD6BA;
  --secondary: #89B0AE;
  --tertiary: #BEE3DB;
  --light: #FAF9F9;
  --dark: #555B6E;
  --darker: #111111;
  --error: tomato;
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: 0.1em;
  background: #f2f2f2;
  color: var(--dark);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
  color: tomato;
  
}

h3 {
  font-size: 1.75rem;
  color: var(--light);
}

h4 {
  font-size: 1.4rem;
  
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

