.flex-row {
  background: var(--dark);
  display: flex;
  flex-wrap: wrap;
  height: 3.1rem;
  align-content: center;
}

nav ul {
  color: var(--light);
  text-decoration: none;
}

nav li {
  list-style: none;
  cursor: pointer;
}

nav a:hover {
  text-decoration: none;
}

